.row {
    background: #e0e0e0;
    width: 100%;
    margin: 10rem auto !important;
}

.row h2 {
    text-align: center;
}

@media only screen and (min-width:560px) {

    .row {
        width: 75%;
    }
}

@media only screen and (min-width:1140px) {

    .row {
        width: 60%;
        height: 30rem;
    }
}